@import "./global.scss";

.App{
    background: linear-gradient(180deg,$grad1,$grad2);
    overflow-x:hidden ;
}
.sections{
    
    
    width: 100vw; 
    height: 100vh ;


    scroll-behavior: smooth;
    
    &::-webkit-scrollbar{
        width: 10px;
        //background-color: rgb(0, 11, 71);
    }
    &::-webkit-scrollbar-thumb {
        direction:rtl;
        border-radius: 5px;
        background: rgb(230, 242, 255);
      }

    > *{
        border-radius: 5px;
        width: 100vw;
        height: 100vh;
        //scroll-snap-align: start;
    }
    .cont{
            padding-top: 0px;

    }

}